import React from "react";
import { Link } from "react-router-dom";
import meLogo from "../image/me-logo.png";

function Navbar(){
    return (
        <nav class="navbar bg-light fixed-top">
  <div class="container-fluid">
    <Link class="navbar-brand" to="/"><img src={meLogo} alt="" srcset="" class="me-logo"/></Link>
    <button class="navbar-toggler" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasNavbar" aria-controls="offcanvasNavbar">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="offcanvas offcanvas-end" tabindex="-1" id="offcanvasNavbar" aria-labelledby="offcanvasNavbarLabel">
      <div class="offcanvas-header">
        <h5 class="offcanvas-title" id="offcanvasNavbarLabel">Welcome Dodo's Home</h5>
        <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
      </div>
      <div class="offcanvas-body">
        <ul class="navbar-nav justify-content-end flex-grow-1 pe-3">
          <li class="nav-item">
          <span data-bs-toggle="offcanvas" data-bs-target=".navbar-offcanvas.show"><Link  class="nav-link active" aria-current="page" to="/">Home</Link ></span>
          </li>
          <li class="nav-item">
          <span data-bs-toggle="offcanvas" data-bs-target=".navbar-offcanvas.show"><Link  class="nav-link" to="/aboutme">About Me </Link></span>
          </li>
          <li class="nav-item">
          <span data-bs-toggle="offcanvas" data-bs-target=".navbar-offcanvas.show"><Link class="nav-link" to="/contact">Contact</Link></span>
          </li>
          <li class="nav-item dropdown">
            <a class="nav-link dropdown-toggle"  role="button" data-bs-toggle="dropdown" aria-expanded="false">
              Work
            </a>
            <ul class="dropdown-menu text-center">
            <span data-bs-toggle="offcanvas" data-bs-target=".navbar-offcanvas.show"><li><Link class="dropdown-item" to="/products">Products</Link></li></span>
            <span data-bs-toggle="offcanvas" data-bs-target=".navbar-offcanvas.show"><li><Link class="dropdown-item" to="/skills">Skills</Link></li></span>
              <li>
                <hr class="dropdown-divider"/>
              </li>
              <span data-bs-toggle="offcanvas" data-bs-target=".navbar-offcanvas.show"><li><Link class="dropdown-item" to="/blog">Blog</Link></li></span>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</nav>
   );
}

export default Navbar;